// UPDATED KEYWORD RECOGNITION
const checkUserInput = ({ value }) => {
  const lowerCaseValue = value.toLowerCase();
  if (lowerCaseValue.includes('project')) return 'project_options';
  if (lowerCaseValue.includes('skill') || lowerCaseValue.includes('tech')) return 'skills';
  if (lowerCaseValue.includes('contact') || lowerCaseValue.includes('email')) return 'contact';
  if (lowerCaseValue.includes('education') || lowerCaseValue.includes('degree') || lowerCaseValue.includes('iit')) return 'education';
  if (lowerCaseValue.includes('work') || lowerCaseValue.includes('experience') || lowerCaseValue.includes('job') || lowerCaseValue.includes('intern')) return 'experience_intro';
  if (lowerCaseValue.includes('certification') || lowerCaseValue.includes('certificate')) return 'certifications_info';
  if (lowerCaseValue.includes('creator') || lowerCaseValue.includes('who made you')) return 'creator_info';
  return 'unrecognized';
};

