// --- THE MAIN COMPONENT ---
function PortfolioChatBot({ theme }) {
  const [isOpen, setIsOpen] = useState(false);
  const [selectedVoice, setSelectedVoice] = useState(null);
  const [showWelcome, setShowWelcome] = useState(true);
  const [isChatStarted, setIsChatStarted] = useState(false);

  useEffect(() => {
    const synth = window.speechSynthesis;
    const loadVoices = () => {
      const voices = synth.getVoices();
      const preferredVoices = ["Google US English", "Samantha", "Microsoft Zira Desktop - English (United States)", "Daniel"];
      const voice = voices.find(v => preferredVoices.includes(v.name)) || voices.find(v => v.lang.startsWith('en-US'));
      setSelectedVoice(voice || voices[0]);
    };
    if (synth.getVoices().length > 0) {
      loadVoices();
    } else {
      synth.onvoiceschanged = loadVoices;
    }
  }, []); 

  const speechConfig = {
    enable: true,
    lang: 'en-US',
    rate: 0.9,
    pitch: 1.1,
    voice: selectedVoice,
  };

  const handleStartChat = () => {
    setShowWelcome(false);
    setIsChatStarted(true);
  };

  const handleToggleChat = () => {
    if (isOpen && window.speechSynthesis) {
      window.speechSynthesis.cancel();
    }
    setIsOpen(prevIsOpen => !prevIsOpen);
    // Reset welcome screen when closing chat
    if (!isOpen) {
      setShowWelcome(true);
      setIsChatStarted(false);
    }
  };

  return (
    <>
      <style>
        {`.typing-indicator { display: inline-block; } .typing-indicator span { height: 8px; width: 8px; float: left; margin: 0 1px; background-color: #9E9EA1; display: block; border-radius: 50%; opacity: 0.4; animation: loadingFade 1s infinite; } .typing-indicator span:nth-of-type(2) { animation-delay: 0.2s; } .typing-indicator span:nth-of-type(3) { animation-delay: 0.4s; } @keyframes loadingFade { 0% { opacity: 0.4; } 50% { opacity: 1; } 100% { opacity: 0.4; } }`}
      </style>

      <FloatingButton 
        onClick={handleToggleChat}
        theme={theme}
        aria-label={isOpen ? "Close chat" : "Open chat with portfolio assistant"}
      >
        <ButtonContent>
          {isOpen ? (
            <span style={{ fontSize: '24px' }}>‚úï</span>
          ) : (
            <Lottie 
              animationData={chatbotLottie}
              loop={true}
              style={{ width: 45, height: 45 }}
            />
          )}
        </ButtonContent>
      </FloatingButton>

      {isOpen && (
        <ChatbotContainer>
          <ThemeProvider theme={botTheme}>
            {selectedVoice && (
              <>
                {showWelcome ? (
                  <WelcomeContainer>
                    <Lottie 
                      animationData={chatbotLottie} 
                      loop={true} 
                      style={{ width: 200, height: 200 }} 
                    />
                    <WelcomeText>Hi there! I'm Laxman's portfolio assistant.</WelcomeText>
                    <StartButton onClick={handleStartChat}>Start Chat</StartButton>
                  </WelcomeContainer>
                ) : (
                  <ChatBot
                    steps={steps}
                    headerTitle="Portfolio Assistant"
                    botAvatar={botAvatarUrl}
                    userAvatar={userAvatarUrl}
                    width="100%"
                    height="500px"
                    style={{
                      background: 'transparent',
                      boxShadow: 'none',
                    }}
                    speechSynthesis={speechConfig}
                  />
                )}
              </>
            )}
          </ThemeProvider>
        </ChatbotContainer>
      )}
    </>
  );
}

export default PortfolioChatBot;